"use client"

import { Card, CardContent } from '@/components/ui/card'
import Link from 'next/link'
import { useRouter } from "next/navigation"
import Api, { Transaction } from '@/app/services/logic';


const api = new Api();

  

export default async function TransactionsLayout({
  children,
}: Readonly<{
  children: React.ReactNode
}>) {

  const router = useRouter();


  try {
    const [transactionsResult, categoriesResult] = await Promise.all([
      api.getTransactions({ page: 1, limit: 1000 }),
      api.getAllCategories()
    ]);
    
   

    const transactions = transactionsResult.data || [];


    // Helper function to get category name
    const getCategoryName = (categoryId: string | number): string => {
      if (!categoryId) return 'Uncategorized';
      
      const category = categoriesResult.find(cat => 
        cat.id == categoryId || 
        String(cat.id) === String(categoryId) ||
        Number(cat.id) === Number(categoryId)
      );
      return category?.name || 'Uncategorized';
    };

    // Format date for display
    const formatDate = (dateString: string) => {
      try {
        return new Date(dateString).toLocaleDateString('en-US', {
          year: 'numeric',
          month: 'short',
          day: 'numeric'
        });
      } catch {
        return 'Invalid Date';
      }
    };

    return (
      <div className="min-h-screen bg-background">
        <div className="container mx-auto p-4 sm:p-6">
          <div className="flex flex-col lg:flex-row gap-6">
            {/* Sidebar - Transactions List */}
            <div className="lg:w-80 shrink-0">
              <div className="sticky top-6">
                <h2 className="text-xl font-bold mb-4 text-foreground">Transactions</h2>
                <div className="space-y-3 max-h-[calc(100vh-120px)] overflow-y-auto">
                  {transactions.length === 0 ? (
                    <Card className="p-4 text-center">
                      <p className="text-muted-foreground">No transactions found</p>
                    </Card>
                  ) : (
                    transactions.map((transaction: Transaction) => (
                      <Card onClick={() => router.push(`/users/${transaction.id}`)}
                        key={transaction.id} 
                        className="hover:bg-accent transition-colors duration-200 border shadow-sm"
                      >
                        <CardContent className="p-4">
                          <Link
                            href={`/transactions/${transaction.id}`}
                            className="block hover:no-underline group"
                          >
                            <div className="space-y-2">
                              <div className="flex justify-between items-start">
                                <h3 className="font-semibold text-foreground group-hover:text-primary">
                                  #{transaction.id}
                                </h3>
                                <span className="text-xs text-muted-foreground bg-muted px-2 py-1 rounded">
                                  {getCategoryName(transaction.category)}
                                </span>
                              </div>
                              
                              <div className="text-sm text-muted-foreground">
                                <p className="flex items-center gap-1">
                                  <svg className="w-3 h-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 7V3m8 4V3m-9 8h10M5 21h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v12a2 2 0 002 2z" />
                                  </svg>
                                  {formatDate(transaction.date)}
                                </p>
                              </div>

                              {transaction.amount && (
                                <p className={`text-sm font-medium ${
                                  Number(transaction.amount) >= 0 
                                    ? 'text-green-600' 
                                    : 'text-red-600'
                                }`}>
                                  {Number(transaction.amount) >= 0 ? '+' : '-'}
                                  ${Math.abs(Number(transaction.amount)).toFixed(2)}
                                </p>
                              )}

                              {transaction.description && (
                                <p className="text-xs text-muted-foreground truncate">
                                  {transaction.description}
                                </p>
                              )}
                            </div>
                          </Link>
                        </CardContent>
                      </Card>
                    ))
                  )}
                </div>
              </div>
            </div>

            {/* Main Content Area */}
            <main className="mt-11">
              <div className="">
                {children}
              </div>
            </main>
          </div>
        </div>
      </div>
    )
  } catch (error) {
    console.error('Error fetching data:', error);
    return (
      <div className="min-h-screen flex items-center justify-center bg-background">
        <div className="text-center space-y-4">
          <div className="w-16 h-16 mx-auto bg-destructive/10 rounded-full flex items-center justify-center">
            <svg className="w-8 h-8 text-destructive" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-2.5L13.732 4c-.77-.833-1.964-.833-2.732 0L4.082 16.5c-.77.833.192 2.5 1.732 2.5z" />
            </svg>
          </div>
          <h1 className="text-2xl font-bold text-foreground">Failed to load transactions</h1>
          <p className="text-muted-foreground">There was an error loading your transaction data.</p>
          <button 
            onClick={() => window.location.reload()}
            className="px-4 py-2 bg-primary text-primary-foreground rounded-md hover:bg-primary/90 transition-colors"
          >
            Try Again
          </button>
        </div>
      </div>
    )
  }
}
